import React, {useState} from 'react';
// import logo from './logo.svg';
import './App.css';

function App() {
  const [formula,setFormula] = useState("");
  const [output,setOutput] = useState("0");
  function clear() {
    setFormula("");
    setOutput("0");
  }
  function operator(op) {
    let prev = formula;
    if(prev.includes("=")){
      prev = output;
    }
    
    if(prev[prev.length - 1] ===op){
    }
    else if(prev==="+" || prev==="/" || prev==="*" || prev==="-"){
      setFormula(op);
    }
    else if((prev[prev.length - 1]==="-" || prev[prev.length - 1]==="+") && (prev[prev.length - 2]==="*" || prev[prev.length - 2]==="/")){
      const prevArray = prev.split('');
      prevArray[prevArray.length - 1] = "";
      prevArray[prevArray.length - 2] = op;
      prev = prevArray.join('');
      setFormula(prev);
    }
    else if((prev[prev.length - 1] === "*"|| prev[prev.length - 1] === "/") && (op==="+" || op==="-")){
      setFormula(prev+op);
    }
    else if(prev[prev.length - 1] === "*"|| prev[prev.length - 1] === "/"){
      const prevArray = prev.split('');
      prevArray[prevArray.length - 1] = op;
      prev = prevArray.join('');
      setFormula(prev);
    }
    else{
      setFormula(prev+op);
    }
    setOutput(op);
  }

  function number(val){
    let prev = formula;
    let valu = output;
    if(prev.includes("=")){
      prev = "";
      valu = "";
    }
    
    setFormula(prev+val);
    if(valu === "+" || valu === "-" || valu === "*" ||valu === "/"||valu==="0"){
      setOutput(val);
    }
    else{
    setOutput(valu+val);}
  }

  function equal(){
    const ans = eval(formula)
    setOutput(ans);
    setFormula(formula+"="+ans);
  }
  function decimal(){
    let prev = formula;
    let valu = output;
    if(prev[prev.length - 1] === "." || valu.includes(".")){

    }
    else{
      setFormula(prev+".");
      setOutput(output+".");
    }
    
  }
  

  return (
    <div className="calculator">
      <div className="formulaScreen">{formula}</div>
      <div className="outputScreen" id="display">{output}</div>
      <div>
        <button className="jumbo" onClick={clear} id="clear" value="AC" style={{background: "rgb(172, 57, 57)"}}>AC</button>
        <button id="divide" value="/" onClick={()=>{operator("/")}} style={{background: "rgb(102, 102, 102)"}}>/</button>
        <button id="multiply" value="x" onClick={()=>{operator("*")}} style={{background: "rgb(102, 102, 102)"}}>x</button>
        <button id="seven" onClick={()=>{number("7")}} value="7">7</button>
        <button id="eight" onClick={()=>{number("8")}} value="8">8</button>
        <button id="nine" onClick={()=>{number("9")}} value="9">9</button>
        <button id="subtract" value="‑" onClick={()=>{operator("-")}} style={{background: "rgb(102, 102, 102)"}}>‑</button>
        <button id="four" onClick={()=>{number("4")}} value="4">4</button>
        <button id="five" onClick={()=>{number("5")}} value="5">5</button>
        <button id="six" onClick={()=>{number("6")}} value="6">6</button>
        <button id="add" value="+" onClick={()=>{operator("+")}} style={{background: "rgb(102, 102, 102)"}}>+</button>
        <button id="one" onClick={()=>{number("1")}} value="1">1</button>
        <button id="two" onClick={()=>{number("2")}} value="2">2</button>
        <button id="three" onClick={()=>{number("3")}} value="3">3</button>
        <button className="jumbo" id="zero" onClick={()=>{number("0")}} value="0">0</button>
        <button id="decimal" onClick={decimal} value=".">.</button>
        <button id="equals" onClick = {equal} value="=" style={{background: "rgb(0, 68, 102)", position: "absolute", height: "130px", bottom: "5px"}}>=</button>
        </div>
    </div>
  );

}

export default App;



